import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { BonReceptionConvertionComponent } from './bon-reception-convertion.component';

const routes: Routes = [
  {
    path: '',
    component: BonReceptionConvertionComponent
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BonReceptionConvertionRoutingModule { }
